import {JetView} 	from "webix-jet";
import outplugins 	from "views/menus/export";
import daterange 	from "views/menus/datebar";
import paging 		from "views/modules/paging";
import eyeFilter 	from "views/modules/groupfilter";
import staFilter 	from "views/modules/statusfilter";
import onlFilter 	from "views/modules/onlinefilter";
import staTemplate 	from "views/templates/status";
import eyeTemplate 	from "views/templates/eye";
import delTemplate 	from "views/templates/deleted";
import onlTemplate 	from "views/templates/online";

export default class ItemView extends JetView {
    config() {
        return layout;
    }
    init(view) {
        webix.extend(view.queryView({view: "datatable"}), webix.ProgressBar);
        $$('history-form').bind($$('history-list'));
      //  webix.dp.$$("history-list").config.updateFromResponse = true;
    }
}

var xml_format = webix.Date.strToDate("%Y-%m-%d");

const hgrid = {
    id: "history-list",
    view: "datatable",
    select: "row",    
    pager: "pagerA",   
    "export": true,

    columns: [
        {id: "item_id", header: "Id", sort: "server", minWidth: 120},
        {map: "(date)#date_changed#", header: "", sort: "server", minWidth: 120, format: webix.Date.dateToStr("%d.%m.%Y")},        
        {id: "deleted", header: "<span class='webix_icon mdi mdi-window-close deleted'></span>", width: 40, css: {"text-align": "center"}, template: delTemplate},
        {id: "ownername", header: ["", {content: "serverSelectFilter", options: "index.php?route=history/history/getOwners&token=" + token}], sort: "server", minWidth: 120, fillspace: 2},
        {id: "wialon_groupname", header: ["", {content: "serverSelectFilter", options: "index.php?route=history/history/getGroups&token=" + token}], sort: "server", minWidth: 120, fillspace: 2},        
        {id: "itemname", header: ["", {content: "serverFilter"}], sort: "server", minWidth: 120, fillspace: 2},
        {id: "tracker_uid", header: [" UID", {content: "serverFilter"}], sort: "server", minWidth: 120, fillspace: 1},
        {id: "trackername", header: ["", {content: "serverSelectFilter", options: "index.php?route=history/history/getTrackers&token=" + token}], sort: "server", minWidth: 120, fillspace: 1},
        {id: "sim1", header: ["SIM-1", {content: "serverFilter"}], sort: "server", minWidth: 120, fillspace: 1},
        {id: "sim2", header: ["SIM-2", {content: "serverFilter"}], sort: "server", minWidth: 120, fillspace: 1},        
        {id: "wialon_group_off", header: ["", {content: "eyeFilter", css: "webix_ss_filter"}], sort: "server", minWidth: 80, css: {"text-align": "center"}, template: eyeTemplate},
        {id: "online", header: ["", {content: "onlFilter", css: "webix_ss_filter"}], sort: "server", minWidth: 120, css: {"text-align": "center"}, template: onlTemplate},
        {id: "price", header: ["", {content: "selectFilter", options: "index.php?route=history/history/getTarifs&token=" + token}], sort: "server", minWidth: 60, fillspace: 1},
        {id: "history_discount_id", header: ["", {content: "selectFilter", options: "index.php?route=history/history/getDiscounts&token=" + token}], sort: "server", minWidth: 60, fillspace: 1},
    ],
    scheme: {
        // init      ( -     - map)
        $init: function (obj) {
         //   if (obj.deleted == 1)
          //      obj.$css = "deleted";
          //  obj.date_changed = xml_format(obj.date_start);
        }
    },
	url:"index.php?route=history/history&token="+token,
    ready: function () {
        webix.extend(this, webix.ProgressBar);
       // Client sorting can't be used with dynamic loading
       // this.sort({by: "date_changed", dir: "desc"}); 
    }
};



const hform = {
    view: "form",
    id: "history-form",
    multiview: {keepViews: true},
    dataFeed: function (id) {
        let current_item_id = $$('history').getItem(id).item_id;
        this.load("index.php?route=catalog/item/getForm&token=" + token + "&item_id=" + current_item_id);
    },
    elements: [
        {         
        
           
        },
    ],
    //rules:{
    //	"date_end": function(value){ return value >= $$("date_start").getValue()},
    //	"date_start": function(value){ return value <= $$("date_end").getValue()},
    //}
};

const views = {
    view: "multiview",
    id: "history-views",
    cells: [hgrid, hform, ]
};

const layout = {
    id: "layout",
    type: "space",
    rows: [
        {height:40, id:"edit-tools", cols: outplugins.concat(daterange) },
        //{height: 40, id: "edit-form-icon", cols: toolplug, hidden: true},
        {rows: [views, paging]}
    ]
};